import React from 'react';
import { NavLink } from 'react-router-dom';
import '../assets/css/home.css';

function Home() {
    return (
        <div className='home_page'>
            <section>
                <div className='content'>
                    <div className='ctn-left'>
                        <h2>Welcome to our website.<br />
                            <span className='gradient'></span>
                        </h2>
                        <p>you can learn everythink from this website.</p>
                        <button className='btn btn-start'>
                            <NavLink to={'/course'}>Start Now</NavLink>
                        </button>
                    </div>
                    <div className='ctn-right'>
                        <img src={require('../assets/images/banner.png')} alt='banner' />
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Home