import React, { useState } from 'react';
import '../assets/css/course.css';
import '../assets/js/main';


function Course() {

    const Cardinfo = [
        {
            topHot: "",
            image: "http://3.bp.blogspot.com/_oN2ovDH18dI/TNKOkWSZeVI/AAAAAAAAAJU/BMk5LbwR9a8/s1600/rupp.jpg",
            title: "Royal University of Phnom Penh",
            dis_title: "The Royal University of Phnom Penh is a national research university of Cambodia, located in the Phnom Penh capital. Established in 1960, it is the country's largest university. It hosts around 20,000 students in undergraduate and postgraduate programmes.",
            price: "--/--",
            cover: "https://is3-ssl.mzstatic.com/image/thumb/Purple123/v4/fe/5a/6b/fe5a6b7c-0d4a-fbb6-20a2-8a199fc43da8/source/512x512bb.jpg",
            name: "RUPP"
        },
        {
            topHot: "",
            image: "https://doe.ifl-rupp.com/wp-content/uploads/2022/02/ifl-doe.png",
            title: "Institute of Foreign Languages",
            dis_title: "The Institute of Foreign Languages is an institute under the Royal University of Phnom Penh Campus. It consists of the department of Chinese, English, French, Japanese, Korean, Thai, and International Studies.",
            price: "--/--",
            cover: "https://is3-ssl.mzstatic.com/image/thumb/Purple123/v4/fe/5a/6b/fe5a6b7c-0d4a-fbb6-20a2-8a199fc43da8/source/512x512bb.jpg",
            name: "IFL"
        },
        {
            topHot: "G26",
            image: "https://leverageedublog.s3.ap-south-1.amazonaws.com/blog/wp-content/uploads/2020/06/22210327/BE-Computer-Science.png",
            title: "Department of Computer Science",
            dis_title: "Computer Science department, Royal University of Phnom Penh, Cambodia.",
            price: "250/y",
            cover: "https://is3-ssl.mzstatic.com/image/thumb/Purple123/v4/fe/5a/6b/fe5a6b7c-0d4a-fbb6-20a2-8a199fc43da8/source/512x512bb.jpg",
            name: "IT - CS"
        }
    ];
    const ShowCard = (Cardinfo, index) => {
        return (
            < li className='card ' key={index} >
                <div className='header'>
                    <span className='bagde'>{Cardinfo.topHot}</span>
                    <div className='header-img'>
                        <img src={Cardinfo.image} ></img>
                    </div>
                </div>
                <div className='card-body'>
                    <div className='title'>
                        <h3>{Cardinfo.title}</h3>
                        <p>{Cardinfo.dis_title}</p>
                    </div>
                    <div className='meta-deteil'>
                        <div className='prifile'>
                            <div className='pf-icon'>
                                <img src={Cardinfo.cover}></img>
                            </div>
                            <div className='pf-name'>{Cardinfo.name}</div>
                        </div>
                        <div className='price'>
                            <p>$<span>{Cardinfo.price}</span></p>
                        </div>
                    </div>
                    {/* <div className='learn-more'>
                                <button type='button' className='btn-lm'>Learn more</button>
                        </div> */}
                </div>
            </li >
        )
    }



    return (
        <div className='course'>
            <div className='wlc-course'>
                <div className='w-bg'>
                    <h1>Course Test</h1>
                </div>
            </div>
            <div className='course-list-main'>
                <ul className='course-list'>
                    {Cardinfo.map(ShowCard)}
                </ul>
            </div>
        </div>
    )
}

export default Course;