import React, { useEffect, useRef, useState } from 'react'
import { Link, NavLink } from 'react-router-dom'
import '../assets/js/main.js';


function Navbar() {
    const [Active, SetActive] = useState(false)
    const activeLink = 'active'
    const noActive = ''

    return (
        <header id='headerTop'>
            <div className="navBar">
                <div className="logo">
                    <h1>
                        <NavLink to={'/'}>DevTeam</NavLink>
                    </h1>
                </div>
                <button id="openBtn" onClick={() => SetActive(!Active)}></button>
                <div className={Active ? "menu menuShow" : "menu"}>
                    <ul className="nav_menu">
                        <button id="closeBtn" onClick={() => SetActive(!Active)}></button>
                        <li><NavLink onClick={() => SetActive(!Active)} className={({ isActive }) => isActive ? activeLink : noActive} to={'/'}>ទំព័រដើម</NavLink></li>
                        <li><NavLink onClick={() => SetActive(!Active)} className={({ isActive }) => isActive ? activeLink : noActive} to={'/course'}>វគ្គសិក្សា</NavLink></li>
                        <li><NavLink onClick={() => SetActive(!Active)} className={({ isActive }) => isActive ? activeLink : noActive} to={'/project'}>គម្រោង</NavLink></li>
                        <li><NavLink onClick={() => SetActive(!Active)} className={({ isActive }) => isActive ? activeLink : noActive} to={'/about'}>អំពីយើង</NavLink></li>
                    </ul>
                    <div id="copyright">
                        <p>© Copyright 2023 <br />
                            All rights reserved by devteam.com</p>
                    </div>
                </div>
            </div>
        </header>
    )
}

export default Navbar;